import { CompassOutlined, LoadingOutlined } from "@ant-design/icons";
import { Col, Row, Statistic } from "antd";
import React, { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import background from "./background.jpg";

import "./HomePage.css";



const Home = () => {

  

  return (
    <div
      className="homepage-banner"
      style={{ backgroundImage: "url(" + background + ")" }}
    >
      <h1 className="page-title main-title">WCA Archive</h1>

      

      <Row gutter={16} justify="center">
        <Col xs={20} md={6}>
          <div className="stat-card">
            <h3>World Championship Articles</h3>
            <p>
              Check out our{" "}
              <Link to="statistics-list"> list of interesting articles about past world championships.</Link>

              <br></br><br></br>

              We are currently working on expanding our collection of articles so unfortunately not all championships are available yet.
            </p>


          </div>
        </Col>
        <Col xs={20} md={6}>
          <div className="stat-card">
            <h3>Speedcubing History</h3>
            <p>
            Check out our article on the general{' '}
            <Link to={'history-of-cubing'}>history of speedcubing.</Link>
            </p>
          </div>
        </Col>
        <Col xs={20} md={6}>
          <div className="stat-card">
            <h3>Event Articles</h3>
            <p>
              Check out our{" "}
              <Link to="statistics-list"> list of interesting articles about WCA events.</Link>

              <br></br><br></br>

              We are currently working on expanding our collection of articles so unfortunately not all events have been covered yet.
            </p>

            
          </div>
        </Col>
      </Row>
    </div>
  );
};

export default Home;
