import React, { useEffect, useRef } from "react";
import "./timeline.css";
import Swiper from "swiper";
import "swiper/swiper-bundle.min.css";
import $ from "jquery"; 

function Timeline() {
  const swiperRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (swiperRef.current) {
      const swiper = new Swiper(swiperRef.current, {
        autoHeight: true,
        autoplay: {
          delay: 5000,
          disableOnInteraction: false,
        },
        speed: 500,
        direction: "horizontal",
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },
        pagination: {
          el: ".swiper-pagination",
          type: "progressbar",
        },
        loop: false,
        effect: "slide",
        spaceBetween: 30,
        on: {
          init: function () {
            $(".swiper-pagination-custom .swiper-pagination-switch")
              .removeClass("active")
              .eq(0)
              .addClass("active");
          },
          slideChangeTransitionStart: function () {
            $(".swiper-pagination-custom .swiper-pagination-switch")
              .removeClass("active")
              .eq(swiper.realIndex)
              .addClass("active");
          },
        },
      });

      $(".swiper-pagination-custom .swiper-pagination-switch").click(function () {
        swiper.slideTo($(this).index());
        $(".swiper-pagination-custom .swiper-pagination-switch")
          .removeClass("active");
        $(this).addClass("active");
      });
    }
  }, []); 

  return (
    <div className="container">
      <div className="swiper-container-wrapper swiper-container-wrapper--timeline">
        <ul className="swiper-pagination-custom">
          <li className="swiper-pagination-switch first active">
            <span className="switch-title">2004</span>
          </li>
          <li className="swiper-pagination-switch">
            <span className="switch-title">2005</span>
          </li>
          <li className="swiper-pagination-switch">
            <span className="switch-title">2006</span>
          </li>
          <li className="swiper-pagination-switch">
            <span className="switch-title">2007</span>
          </li>
          <li className="swiper-pagination-switch last">
            <span className="switch-title">2008</span>
          </li>
        </ul>

        <div className="swiper-pagination swiper-pagination-progressbar swiper-pagination-horizontal"></div>

        <div className="swiper swiper-container swiper-container--timeline" ref={swiperRef}>
          <div className="swiper-wrapper">
            <div className="swiper-slide">
              <span className="title">Title 1</span>
            </div>
            <div className="swiper-slide">
              <span className="title">Title 2</span>
            </div>
            <div className="swiper-slide">
              <span className="title">Title 3</span>
            </div>
            <div className="swiper-slide">
              <span className="title">Title 4</span>
            </div>
            <div className="swiper-slide">
              <span className="title">Title 5</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Timeline;
