import "./AboutPage.css";

const AboutPage = () => {
  return (
    <div>
      <h1 className="page-title">About</h1>
      <center>
        <img src={require("./logo.png")} width={'50px'} height={'50px'}></img>
        <p>
        The WCA Archive is a collection of interesting articles on the WCA's
        history.

        <br></br><br></br>

        This website was created and is managed by members of the WAT. 
      </p>
      </center>
    </div>
  );
};

export default AboutPage;
